* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.app {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 768px) {
  .app {
    width: 750px;
  }
}
@media (min-width: 992px) {
  .app {
    width: 970px;
  }
}
@media (min-width: 1200px) {
  .app {
    width: 1170px;
  }
}
